

/* Flot chart */
.flot-charts-height {
  height: 320px;
}

.flotTip {
  padding: 8px 12px;
  background-color: var(--#{$prefix}dark);
  z-index: 100;
  color: var(--#{$prefix}gray-100);
  box-shadow: $box-shadow;
  border-radius: 4px;
}

.legendLabel{
  color: var(--#{$prefix}gray-500);
}