//
// Form editors.scss
//

.tox-tinymce {
  border: 1px solid var(--#{$prefix}border-color)  !important;
}

.tox {
  .tox-menu{
    background-color: var(--#{$prefix}card-bg) !important;
    border-color: var(--#{$prefix}gray-300) !important;
  }
  .tox-collection--toolbar{
    .tox-collection__item--active{
      background-color: var(--#{$prefix}gray-300) !important;
    }
  }
  .tox-statusbar {
    border-top: 1px solid var(--#{$prefix}border-color)  !important;
  }
    .tox-menubar, .tox-edit-area__iframe, .tox-statusbar{
      background-color: var(--#{$prefix}card-bg)  !important;
      background: none !important;
    }
    .tox-mbtn{
      color: var(--#{$prefix}gray-700)  !important;
        &:hover:not(:disabled):not(.tox-mbtn--active){
            background-color: var(--#{$prefix}gray-300) !important;
        }
    }
    .tox-tbtn{
        &:hover{
            background-color: var(--#{$prefix}white) !important;
        }
    }

    .tox-split-button{
      &:hover{
        box-shadow: 0 0 0 var(--#{$prefix}gray-200) !important;
      }
    }
    .tox-toolbar, .tox-toolbar__overflow, .tox-toolbar__primary{
      background: var(--#{$prefix}light) !important;
      border-top: 1px solid var(--#{$prefix}border-color)  !important;
    }

    .tox-tbtn{
      color: var(--#{$prefix}gray-700) !important;
      svg{
        fill: var(--#{$prefix}gray-700) !important;
      }
    }

    .tox-edit-area__iframe{
      background-color: var(--#{$prefix}card-bg)  !important;
    }
  
    .tox-statusbar a, .tox-statusbar__path-item, .tox-statusbar__wordcount{
      color: var(--#{$prefix}gray-700) !important;
    }

    &:not([dir=rtl]) .tox-toolbar__group:not(:last-of-type) {
      border-right: 1px solid var(--#{$prefix}gray-300) !important;
    } 
}

// Form xeditable
.editable{
  color: var(--#{$prefix}primary);
}
.editable-empty{
  color: var(--#{$prefix}danger);
}