#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1002;
  background-color: var(--bs-header-bg);
  box-shadow: 0 2px 3px -2px rgba(0, 0, 0, 0.15);
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: 70px;
  padding: 0 calc(1.5rem / 2) 0 0;
}
.navbar-header .dropdown .show.header-item {
  background-color: var(--bs-gray-100);
}

.navbar-brand-box {
  padding: 0 1.5rem;
  width: 240px;
  height: 70px;
}

.logo {
  line-height: 70px;
}
.logo .logo-sm {
  display: none;
}

.page-title {
  margin: 0;
  line-height: 70px;
  font-size: 20px;
}

[data-layout=horizontal] .search-wrap {
  width: 100%;
}

/* Search */
.search-wrap {
  background-color: var(--bs-topbar-search-bg);
  color: var(--bs-dark);
  z-index: 9997;
  position: absolute;
  top: 0;
  display: flex;
  width: calc(100% - 240px);
  right: 0;
  height: 70px;
  padding: 0 15px;
  transform: translate3d(0, -100%, 0);
  transition: 0.3s;
}
.search-wrap form {
  display: flex;
  width: 100%;
}
.search-wrap .search-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.search-wrap .search-input {
  flex: 1 1;
  border: none;
  outline: none;
  box-shadow: none;
  background-color: transparent;
}
.search-wrap .close-search {
  width: 36px;
  height: 64px;
  line-height: 64px;
  text-align: center;
  color: inherit;
  font-size: 24px;
}
.search-wrap .close-search:hover {
  color: #ea553d;
}

.search-wrap.open {
  transform: translate3d(0, 0, 0);
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: var(--bs-body-color);
}

@media (max-width: 992px) {
  .navbar-brand-box {
    width: auto;
  }

  .logo span.logo-lg {
    display: none;
  }
  .logo span.logo-sm {
    display: inline-block;
  }
}
.page-content {
  padding: calc(70px + 1.5rem) calc(1.5rem / 2) 60px calc(1.5rem / 2);
}

.header-item {
  height: 70px;
  box-shadow: none !important;
  color: var(--bs-header-item-color);
  border: 0;
  border-radius: 0px;
}
.header-item:hover {
  color: var(--bs-header-item-color);
}

.header-profile-user {
  height: 36px;
  width: 36px;
  background-color: #eeeeee;
}

.noti-icon i {
  font-size: 28px;
  color: var(--bs-header-item-color);
}
.noti-icon .badge {
  position: absolute;
  top: 15px;
  right: 2px;
}

.notify-item img {
  float: right;
  margin-top: 5px;
}

.notification-item .d-flex {
  padding: 0.75rem 1rem;
}
.notification-item .d-flex:hover {
  background-color: var(--bs-dropdown-link-hover-bg);
}

.dropdown-icon-item {
  display: block;
  border-radius: 3px;
  line-height: 34px;
  text-align: center;
  padding: 15px 0 9px;
  display: block;
  border: 1px solid transparent;
  color: #848f98;
}
.dropdown-icon-item img {
  height: 24px;
}
.dropdown-icon-item span {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.dropdown-icon-item:hover {
  border-color: #f3f3f3;
}

.fullscreen-enable [data-bs-toggle=fullscreen] .mdi-fullscreen::before {
  content: "\f0294";
}

