.footer {
  bottom: 0;
  padding: 20px calc(1.5rem / 2);
  position: absolute;
  right: 0;
  color: var(--bs-footer-color);
  left: 240px;
  height: 60px;
  background-color: var(--bs-footer-bg);
}

@media (max-width: 991.98px) {
