// :root CSS dark variables


[data-layout-mode="dark"]{

  // Color system - Dark Mode only

  $gray-100: #212529;
  $gray-200: #2f3d46;
  $gray-300: #374650;
  $gray-400: #dddddd;
  $gray-500: #bec2c5;
  $gray-600: #848f98;
  $gray-700: #f6f6f6;
  $gray-800: #eff2f7;
  $gray-900: #f8f9fa;
  $black:    #000;

  $grays: (
      "100": $gray-100,
      "200": $gray-200,
      "300": $gray-300,
      "400": $gray-400,
      "500": $gray-500,
      "600": $gray-600,
      "700": $gray-700,
      "800": $gray-800,
      "900": $gray-900
  );

  // Prefix for :root CSS variables
  @each $color, $value in $grays {
      --#{$prefix}gray-#{$color}: #{$value};
  }

  // Vertical Sidebar  Light

    // Vertical Sidebar - Default Light
    --#{$prefix}sidebar-bg: #ffffff;
    --#{$prefix}sidebar-menu-item-color: #707070;
    --#{$prefix}sidebar-menu-sub-item-color:#707070;
    --#{$prefix}sidebar-menu-item-icon-color: #707070;
    --#{$prefix}sidebar-menu-item-hover-color: #383c40;
    --#{$prefix}sidebar-menu-item-active-color: #67a8e4;
    --#{$prefix}sidebar-menu-item-active-bg : #f5f5f5;
  
    // Vertical Sidebar - Dark
    --#{$prefix}sidebar-dark-bg: #2f3d46; //2c313a
    --#{$prefix}sidebar-dark-menu-item-color: rgba(255,255,255,0.5);
    --#{$prefix}sidebar-dark-menu-sub-item-color:rgba(255,255,255,0.5);
    --#{$prefix}sidebar-dark-menu-item-icon-color: rgba(255,255,255,0.5);
    --#{$prefix}sidebar-dark-menu-item-hover-color:#ffffff;
    --#{$prefix}sidebar-dark-menu-item-active-color:#ffffff;
    --#{$prefix}sidebar-dark-menu-item-active-bg: #33424c;
  
    // Topbar - Deafult Light
    --#{$prefix}header-bg:                                  #2a363e;
    --#{$prefix}header-item-color:                          #ffffff;
  
    // Topbar - Dark
    --#{$prefix}header-dark-bg:                               #35454f;
    --#{$prefix}header-dark-item-color:                       #ffffff;
  
    // Topbar Search
    --#{$prefix}topbar-search-bg:                             #374852;
  
    // Footer
    --#{$prefix}footer-bg:                                     #2a363e;
    --#{$prefix}footer-color:                                  #dddddd;
  
    // Horizontal nav
    --#{$prefix}topnav-bg:                                     #2f3d46;
    --#{$prefix}menu-item-color:                               #979ea3;
    --#{$prefix}menu-item-active-color:                        #67a8e4;
  
    // body
    --#{$prefix}body-bg:                                       #1f2c33;
    --#{$prefix}body-color:                                     #{$gray-400};
    --#{$prefix}body-color-rgb:                                 #{to-rgb($body-color)};
  
    // Boxed layout width
    --#{$prefix}boxed-body-bg:                                  #e3e3e3;
  
    // apex charts
    --#{$prefix}apex-grid-color:                                #f7f7f7;

    // apex charts
    --#{$prefix}apex-grid-color:                                 #293037;

      // Display
    --#{$prefix}display-none:                                     block;
    --#{$prefix}display-block:                                    none;

    // card-title-desc
  --#{$prefix}card-title-desc:                                    #{$gray-400};

  // twocolumn menu
  --#{$prefix}twocolumn-menu-iconview-bg:                         #{$white};
  --#{$prefix}twocolumn-menu-bg:                                  #{$white};
    
  // two column dark
  --#{$prefix}twocolumn-menu-iconview-bg-dark:                     var(--#{$prefix}vertical-menu-bg-dark);
  --#{$prefix}twocolumn-menu-bg-dark:                              #30363a;
  --#{$prefix}twocolumn-menu-item-color-dark:                      var(--#{$prefix}vertical-menu-item-color-dark);
  --#{$prefix}twocolumn-menu-item-active-color-dark:               #{$white};
  --#{$prefix}twocolumn-menu-item-active-bg-dark:                  #{rgba($white, .15)};

  // boxed 
  --#{$prefix}boxed-body-bg:                                       #{darken($gray-100, 4%)};

  // heading-color
  --#{$prefix}heading-color:                                       #{$gray-700};
  
  // component variable
  --#{$prefix}white:                                               #{$gray-200};
  --#{$prefix}light:                                               #{$gray-300};
  --#{$prefix}light-rgb:                                           #{to-rgb($gray-400)};
  --#{$prefix}dark:                                                #{$gray-800};
  --#{$prefix}dark-rgb:                                            #{to-rgb($gray-800)};
  --#{$prefix}text-muted:                                          #{$gray-500};

  // link
  --#{$prefix}link-color:                                          #{$gray-700};
  --#{$prefix}link-hover-color:                                    #{$gray-700};

  // Border variable
  --#{$prefix}border-color:                                        #{$gray-300};

  // dropdown
  .dropdown-menu{
    --#{$prefix}dropdown-bg:                                        #2f3d46;
  --#{$prefix}dropdown-link-hover-bg:                               #{$gray-300};
  --#{$prefix}dropdown-link-color:                                  #{$gray-500};
  }
  --#{$prefix}dropdown-link-hover-color:                            #{shade-color($gray-900, 5%)};
  --#{$prefix}dropdown-border-width:                                1px;

  // card
  .card{
    --#{$prefix}card-bg:                                             #{$gray-200};
    --#{$prefix}card-cap-bg:                                         #{$gray-200};
    --#{$prefix}card-border-color:                                   #{$gray-300};
  }

  --#{$prefix}card-logo-dark:                                        none;
  --#{$prefix}card-logo-light:                                       block;

  // modal
  .modal{
    --#{$prefix}modal-bg:                                             #{$gray-200};
    --#{$prefix}modal-content-bg:                                     #{$gray-200};
    --#{$prefix}modal-content-border-color:                           #{$gray-300};
  }

  //nav
  .nav{
    --#{$prefix}nav-link-hover-color:                                 #{darken($blue, 15%)};
  }

  // nav tabs
  .nav-tabs{
    --#{$prefix}nav-tabs-link-active-color:                           #{$gray-900};
    --#{$prefix}nav-tabs-link-active-bg:                              #{$gray-200};
  }                                  

  // accordion
  .accordion{
    --#{$prefix}accordion-button-active-color:                         #{$white};
    --#{$prefix}accordion-bg:                                          #{transparent};
    --#{$prefix}accordion-button-bg:                                   #{$gray-300};
    --#{$prefix}accordion-button-active-bg:                            #{$gray-300};
    --#{$prefix}accordion-color:                                       var(--#{$prefix}body-color);
  }

  // table
  .table{
    --#{$prefix}table-color:                                           #{$gray-400};
  }

  .table-light{
    --#{$prefix}table-bg:                                              #{$gray-200};
    --#{$prefix}table-color:                                           #{$gray-400};
    --#{$prefix}table-border-color:                                    #{$gray-300};
  }
  // Boxed layout 
  --#{$prefix}boxed-body-bg:                                           #1a2429;

  // progress
  .progress{
  --#{$prefix}progress-bg:                                              #{$gray-300};
  }

  // toast
  --#{$prefix}toast-background-color:                                   #{rgba($gray-300, .85)};
  --#{$prefix}toast-border-color:                                       #{rgba($white, .1)};
  --#{$prefix}toast-header-border-color:                                #{rgba($white, .05)};

  //list
  .list-group{
    --#{$prefix}list-group-hover-bg:                                     #{lighten($gray-200, 2.5%)};
    --#{$prefix}list-group-bg:                                           #{$gray-200};
  }
 
  // popover
  .popover{
  --#{$prefix}popover-bg:                                                #{lighten($gray-200, 1%)};
  }
  // pagination
  .pagination{
  --#{$prefix}pagination-hover-bg:                                       #{lighten($gray-200, 4%)};
  }
  //form
  --#{$prefix}input-bg:                                                   #33414a;
  --#{$prefix}input-group-addon-border-color:                             #{$gray-300};
  --#{$prefix}input-border:                                               #{$gray-300};
  --#{$prefix}input-border-color:                                         #{$gray-300};
  --#{$prefix}input-focus-border:                                         #{lighten($gray-300, 2%)};
  --#{$prefix}input-disabled-bg:                                          #{$gray-200};

  // input-group-addon
  --#{$prefix}input-placeholder-color:                                    #{$gray-600};
  --#{$prefix}input-group-addon-bg:                                       #{$gray-300};

  //check
  --#{$prefix}input-check-border:                                         #{lighten($gray-300, 4%)};

}